import React from "react";
import { Link } from "gatsby";

interface Post {
  frontmatter: {
    slug: string;
    title: string;
    date: string;
  };
}

const PostLink: React.FC<{ post: Post }> = ({ post }) => (
  <div>
    <Link to={"../articles"+post.frontmatter.slug}>
      {post.frontmatter.title} ({post.frontmatter.date})
    </Link>
  </div>
);

export default PostLink;
